  padding: 20px;
  font-family: Arial, sans-serif;
}

/* 魔法棒图标样式 */
.gjs-toolbar-item[data-id="ai-style-wand"] svg {
  fill: currentColor;
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: middle;
}

.gjs-toolbar-item[data-id="ai-style-wand"]:hover svg {
  color: #4facfe;
}

.ai-style-wand-modal-header h3 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #333;
}

.ai-style-wand-modal-body {
  margin-bottom: 20px;
}

.ai-style-textarea {
  width: 100%;
  min-height: 100px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  resize: vertical;
}

.ai-style-suggestions {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 10px;
}

.ai-style-tag {
  display: inline-block;
  padding: 5px 10px;
  background-color: #f0f0f0;
  border-radius: 20px;
  font-size: 12px;
  color: #333;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.ai-style-tag:hover {
  background-color: #e0e0e0;
}

.ai-style-wand-modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.ai-style-apply-btn, .ai-style-cancel-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.ai-style-apply-btn {
  background-color: #4CAF50;
  color: white;
}

.ai-style-apply-btn:hover {
  background-color: #45a049;
}

.ai-style-cancel-btn {
  background-color: #f5f5f5;
  color: #333;
}

.ai-style-cancel-btn:hover {
  background-color: #e0e0e0;
} `, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,8BAA8B;AAChC;;AAEA,YAAY;AACZ;EACE,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,qBAAqB;EACrB,sBAAsB;AACxB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,iBAAiB;EACjB,aAAa;EACb,sBAAsB;EACtB,kBAAkB;EAClB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,eAAe;EACf,QAAQ;EACR,gBAAgB;AAClB;;AAEA;EACE,qBAAqB;EACrB,iBAAiB;EACjB,yBAAyB;EACzB,mBAAmB;EACnB,eAAe;EACf,WAAW;EACX,eAAe;EACf,sCAAsC;AACxC;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,yBAAyB;EACzB,SAAS;AACX;;AAEA;EACE,iBAAiB;EACjB,YAAY;EACZ,kBAAkB;EAClB,eAAe;EACf,eAAe;AACjB;;AAEA;EACE,yBAAyB;EACzB,YAAY;AACd;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;EACzB,WAAW;AACb;;AAEA;EACE,yBAAyB;AAC3B","sourcesContent":[".ai-style-wand-modal {\n  padding: 20px;\n  font-family: Arial, sans-serif;\n}\n\n/* 魔法棒图标样式 */\n.gjs-toolbar-item[data-id=\"ai-style-wand\"] svg {\n  fill: currentColor;\n  width: 16px;\n  height: 16px;\n  display: inline-block;\n  vertical-align: middle;\n}\n\n.gjs-toolbar-item[data-id=\"ai-style-wand\"]:hover svg {\n  color: #4facfe;\n}\n\n.ai-style-wand-modal-header h3 {\n  margin-top: 0;\n  margin-bottom: 20px;\n  color: #333;\n}\n\n.ai-style-wand-modal-body {\n  margin-bottom: 20px;\n}\n\n.ai-style-textarea {\n  width: 100%;\n  min-height: 100px;\n  padding: 10px;\n  border: 1px solid #ddd;\n  border-radius: 4px;\n  font-size: 14px;\n  resize: vertical;\n}\n\n.ai-style-suggestions {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 8px;\n  margin-top: 10px;\n}\n\n.ai-style-tag {\n  display: inline-block;\n  padding: 5px 10px;\n  background-color: #f0f0f0;\n  border-radius: 20px;\n  font-size: 12px;\n  color: #333;\n  cursor: pointer;\n  transition: background-color 0.2s ease;\n}\n\n.ai-style-tag:hover {\n  background-color: #e0e0e0;\n}\n\n.ai-style-wand-modal-footer {\n  display: flex;\n  justify-content: flex-end;\n  gap: 10px;\n}\n\n.ai-style-apply-btn, .ai-style-c